.form-cont {
  height: 700px;
  background-color:#131212e8;
  flex-direction: column;
  justify-content: center;
  display: flex;
  max-width: 1650px;
  margin: 0 auto;
}
.form {
  display: flex;
  padding: 0;

  max-width: 960px;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  color:#999;

  margin: 0 auto;
}
@media (min-width: 960px) {
  .form {
    max-width: 1650px;
    flex-direction: row;
    justify-content: center;
  }
}

.form__title {
  font-family: "Montserrat", sans-serif;
  margin: 33px;
  font-size: 25px;
}
@media (min-width: 960px) {
  .form__title {
    margin: 0px 55px 293px 0;
  }
}

.form__content {
  display: grid;
  gap: 16px;

  width: 268px;
}
@media (min-width: 960px) {
  .form__content {
    width: 326px;
  }
}
.form__title-label {
  font-family: "Poppins", sans-serif;
  font-size: 13px;
  margin: 0;
  font-weight: 100;
}
.form__content-label {
  font-family: "Poppins", sans-serif;
}
.textarea {
  margin: 0px;
  width: 100%;
  height: 88px;
  background-color: white;
}
.form__content-input {
  display: inline-flex;
  width: 100%;
  height: 32px;
  border-radius: 4px;
}

.form__content-button-container {
  width: 100%;
}
.form__content-label-button-enviar {
  width: 100%;
  background-color: #2c2c2c;
  border-radius: 3px;
  cursor: pointer;
  border: none;
  height: 33px;
  color: white;
  font-family: "Poppins", sans-serif;
  background-image: linear-gradient(to right, #2c2c2c, #555555); /* Cambia el color a uno más claro de izquierda a derecha */
  transition: background-color 1.3s ease; /* Agrega una transición suave */
}

.form__content-label-button-enviar:hover {
  background-image: none; /* Elimina el gradiente al pasar el mouse */
  background-color: #555555; /* Cambia el color a uno más claro */
}

